@import url(https://fonts.googleapis.com/css2?family=Turret+Road&display=swap);
*{box-sizing:border-box;-webkit-box-sizing:border-box;-moz-box-sizing:border-box;margin:0;padding:0}body{background:#311b92;background-image:url("https://www.transparenttextures.com/patterns/gradient-squares.png");min-height:70vh;padding:1rem;box-sizing:border-box;display:flex;justify-content:center;align-items:center;color:#494a4b;font-family:"Turret Road",cursive;text-align:center;font-size:130%}.container{background:#ffd600;border-radius:20px;box-shadow:4px 3px 7px 2px #00000040;padding:1rem 1rem 2rem;display:flex;flex-direction:column;width:100%;height:auto;min-height:500px;max-width:600px;min-width:500px}.heading{display:flex;align-items:center;justify-content:center;margin:2rem 2rem}.heading h1{color:#ddd;transform:rotate(-6deg);padding:0.2rem 1.2rem;border-radius:5px;background-color:#311b92;font-size:1.5rem}.form{margin-bottom:32px}.form .task-input{padding:14px 32px 14px 16px;border-radius:4px 0 0 4px;border:2px solid #5d0cff;outline:none;width:320px;background:transparent;color:#333}.form .task-input::-webkit-input-placeholder{color:#333}.form .task-input:-ms-input-placeholder{color:#333}.form .task-input::-ms-input-placeholder{color:#333}.form .task-input::placeholder{color:#333}.form .task-input.edit{border:2px solid #149fff}.form .task-button{padding:16px;border:none;border-radius:0 4px 4px 0;cursor:pointer;outline:none;background:linear-gradient(90deg, #5d0cff 0%, #9b00fa 100%);color:#ddd;text-transform:capitalize}.form .task-button.edit{background:linear-gradient(90deg, #149fff 0%, #117aff 100%);padding:16px 22px}.taskItem{display:flex;justify-content:space-between;align-items:center;margin:4px auto;color:#ddd;background:linear-gradient(90deg, #ff7614 0%, #ff5411 100%);padding:16px;border-radius:5px;width:90%}.taskItem:nth-child(4n+1){background:linear-gradient(90deg, #5d0cff 0%, #9b00fa 100%)}.taskItem:nth-child(4n+2){background:linear-gradient(90deg, #ff0cf1 0%, #fa0087 100%)}.taskItem:nth-child(4n+3){background:linear-gradient(90deg, #149fff 0%, #117aff 100%)}.taskItem.complete{text-decoration:line-through;opacity:0.4}.icons{display:flex;align-items:center;font-size:24px;cursor:pointer}.icons .editIcon{color:#fff}.icons .deleteIcon{margin-right:5px;color:#fff}ul{list-style-type:none}ul li{text-align:left;position:relative;padding:0.5rem}

